@import "base";

* {
  margin: 0;
  padding: 0;
}

.header {
  background-color: #000;
  color: #fff;
  margin-top: -1rem;
  padding: 1em;
  a {
    color: #fff;
    text-decoration: none;
    font-weight: bold;
    letter-spacing: -0.05rem;
  }
  a:visited {
    color: #fff;
  }
}

.banner {
  width: 100%;
  padding: 0;
  margin: 0;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 1em;
}

.title-section {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 1em;
  margin: 2em 0;
}

.page-title { font-size: 2.5em; }

.tagline {
  font-style: italic;
  font-weight: 100;
}

.flavor-text {
  font-style: italic;
  font-weight: 100;
  font-size: 0.75em;
  padding: 1em;
  background-color: #eee;
  height: fit-content;
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 1em;
}

.breadcrumbs {
  width: 100%;
  display: flex;
  flex-direction: row-reverse;
  padding: 1rem 0;
  gap: 1rem;
}

.page-content {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 1em;
}

.layout-grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 1em;
}

.index-grid {
  column-count: 4;
  section {
    margin-bottom: 1rem;
    display: flex;
    flex-direction: column;
    break-inside: avoid;
  }
}

details {
  background: #EEE;
  padding: 1rem;
  summary {
    font-weight: bold;
    cursor: pointer;
    padding: 1rem;
  }
}



/* Responsive layout adjustments */
@media (max-width: 768px) {
  .index-grid {
    column-count: 2; /* Reduce to 2 columns on smaller screens */
  }
}


ul.unstyled {
  list-style: none;
  margin: 0;
  padding: 0;
}

hr { margin: 2em 0; }
dl {
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-gap: 1em;
}
dt {
  font-weight: bold;
  grid-column: 1; /* Place in column 1 */
}
dd {
  margin: 0;
  grid-column: 2; /* Place in column 2 */
}

table {
  border-collapse: collapse;
  width: fit-content;
}

tbody tr:nth-child(odd) {
  background-color: #EEE; /* Alternate color for even rows */
}

th, td {
  padding: 0.5em;
  border: 1px solid #CCC;
}

.sidebar {
  background: #EEE;
  padding: 1rem;
  height: fit-content;
  ul {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 1em;
    li {
      display: grid;
      grid-template-columns: 1fr;
    }
  }
}

.sidebar-grid {
  display: grid;
  grid-template-columns: 2fr 3fr;
  grid-gap: 1em;
}

@media (max-width: 768px) {
  .sidebar-grid {
    grid-template-columns: 1fr; /* Single column for mobile */
  }
}

.splash-container {
  position: relative;
  width: 100%; /* Set to your image's width */

  background: linear-gradient(315deg, #EFC237,  #AD6CDC,);;
  overflow: hidden; /* Hide the overflowing image */
}

.splash-container::before {
  content: "";
  display: block;
  padding-top: 33.3%; /* 16:9 Aspect Ratio */
}

.splash-container img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;

  object-fit: cover; /* This ensures the images cover the container fully */
}

.multiply {
  z-index: 10;
  width: 100%;
  filter: grayscale(85%) contrast(150%);
  mix-blend-mode: hard-light; /* Apply multiply blend mode with background */
}

img {
  max-width: 100%;
  height: auto;
  display: block;
  margin: 0 auto;
}